<template>
  <div class="partner-company">
    <el-container>
      <el-header class="header">
        <!-- 搜索表单 start -->
        <el-form
          :inline="true"
          :model="searchValidateForm"
          ref="searchValidateForm"
          label-width="100px"
          class="search-validate-form"
          :rules="searchValidateFormRules"
        >
          <el-form-item label="" prop="memberName">
            <!-- 人员名称 start -->
            <el-input
              class="input-name"
              v-model="searchValidateForm.memberName"
              placeholder="姓名"
            ></el-input>
            <!-- 人员名称部nd -->
          </el-form-item>
        </el-form>
        <!-- 搜索表单 end -->

        <!-- 查询数据 start -->
        <el-button
          type="primary"
          icon="el-icon-search"
          @click="onSearch('searchValidateForm')"
          >搜索</el-button
        >
        <!-- 查询数据 end -->

        <!-- 重置 start -->
        <el-button type="primary" icon="el-icon-refresh-right" @click="onReset"
          >重置</el-button
        >
        <!-- 重置 end -->

        <!-- 新增 start -->
        <el-button
          type="primary"
          icon="el-icon-circle-plus-outline"
          @click="onAdd"
          >新增人员</el-button
        >
        <!-- 新增 end -->
      </el-header>

      <el-main>
        <!-- 表格 start -->
        <el-table
          class="table"
          :data="this.tableData"
          style="width: 100%"
          border
          v-loading="tableLoading"
        >
          <el-table-column label="姓名" min-width="130" fixed="right">
            <template slot-scope="scope">
              <div>
                {{ scope.row.memberName }}
              </div>
            </template>
          </el-table-column>
          <el-table-column label="所属部门" min-width="130" fixed="right">
            <template slot-scope="scope">
              <div>
                {{ scope.row.departmentNames }}
              </div>
            </template>
          </el-table-column>
          <el-table-column label="职位" min-width="120" fixed="right">
            <template slot-scope="scope">
              <div>
                {{ scope.row.position }}
              </div>
            </template>
          </el-table-column>
          <el-table-column label="手机号" min-width="130" fixed="right">
            <template slot-scope="scope">
              <div>
                {{ scope.row.phone }}
              </div>
            </template>
          </el-table-column>
          <el-table-column label="操作" min-width="130" fixed="right">
            <template slot-scope="scope">
              <el-button
                @click.native.prevent="editRow(scope.$index, scope.row)"
                type="text"
                size="small"
              >
                编辑
              </el-button>
              <el-button
                @click.native.prevent="deleteRow(scope.$index, scope.row)"
                type="text"
                size="small"
              >
                删除
              </el-button>
            </template>
          </el-table-column>
        </el-table>
        <!-- 表格 end -->
      </el-main>

      <el-footer>
        <!-- 分页 start -->
        <div class="block">
          <el-pagination
            background
            @size-change="onSizeChange"
            @current-change="onCurrentChange"
            :current-page.sync="currentPage"
            :page-sizes="[10, 20, 30, 40]"
            :page-size="pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total"
          >
          </el-pagination>
        </div>
        <!-- 分页 end -->
      </el-footer>

      <!-- dialog对话框 start -->
      <!-- 新增 dialog start -->
      <el-dialog
        class="dialog"
        width="50%"
        :title="dialogTitle"
        :visible.sync="dialogVisible"
        :before-close="handleClose"
        :close-on-click-modal="false"
      >
        <!-- form表单 -->
        <el-form
          :model="validateForm"
          ref="validateForm"
          label-width="80px"
          class="demo-ruleForm"
          :rules="validateFormRules"
        >
          <!-- 人员姓名 start -->
          <el-form-item label="姓名" prop="memberName">
            <el-input
              v-model="validateForm.memberName"
              placeholder="请输入姓名"
            ></el-input>
          </el-form-item>
          <!-- 人员姓名 end -->

          <!-- 部门名称 start -->
          <el-form-item label="所属部门" prop="departmentNames">
            <el-select
              v-model="selectedDepartment"
              placeholder="请选择部门"
              @change="handleDepartmentChange"
            >
              <el-option
                v-for="item in departmentNamelist"
                :key="item.value"
                :label="item.label"
                :value="item.departmentName"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 部门名称 end -->

          <!-- 人员职位 start -->
          <el-form-item label="职位" prop="position" label-width="80px">
            <el-input
              v-model="validateForm.position"
              placeholder="请输入职位"
            ></el-input>
          </el-form-item>
          <!-- 人员职位 end -->

          <!-- 手机号 start -->
          <el-form-item label="手机号" prop="phone">
            <el-input
              v-model="validateForm.phone"
              placeholder="请输入手机号"
            ></el-input>
          </el-form-item>
          <!--手机号 end -->
        </el-form>
        <!-- form表单 -->

        <!-- 提交 start -->
        <div class="submit">
          <el-button type="primary" @click="submitForm('validateForm')"
            >提交</el-button
          >
          <el-button @click="dialogClose">取消</el-button>
        </div>
        <!-- 提交 end -->
      </el-dialog>
      <!-- dialog对话框 end -->
    </el-container>
  </div>
</template>

<script>
// import JsonExcel from 'vue-json-excel'
import FileSaver from "file-saver";
import XLSX from "xlsx";

import { formatMonth, createUrl, formatDate } from "../utils/utils.js";
export default {
  name: "memberList",
  data() {
    /**
     * 公共验证函数(验证数字值)
     * @params message - 提示信息
     */
    function checkNumberMethod(name = "") {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          // 如果值为空，可以回调
          callback();
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + "必须为数字值"));
        } else {
          callback();
        }
      };
    }

    /**
     * 公共验证函数(验证必填数字值)
     * @params message - 提示信息
     */
    function checkRequireNumberMethod(name = "") {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + "不能为空"));
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + "必须为数字值"));
        } else {
          callback();
        }
      };
    }

    /**
     * 公共验证函数(验证必填值)
     * @params message - 提示信息
     */
    function checkRequireMethod(name = "") {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + "不能为空"));
        } else {
          callback();
        }
      };
    }

    return {
      approveDialogVisible: false,
      dialogTitle: "",
      appRoverData: [], //审批人
      showTable: true, // 是否显示表格
      tableData: [], // 表格数据
      tableLoading: false,

      searchCompanyOptions: [], // 搜索的公司选项
      searchScenicOptions: [], // 搜索的景区选项

      exportTableData: [], // 导出的全部表格数据
      downloadLoading: false, // 导出excel按钮的加载动画

      currentPage: 1, // 当前页数,
      pageSize: 10, // 每页显示条数

      dialogVisible: false, // 是否显示添加dialog对话框
      editDialogVisible: false, // 是否显示编辑dialog对话框
      detailDialogVisible: false, // 是否显示详情dialog对话框

      detailTableList: [], // 详情列表

      dialogState: "", // dialog是新增还是编辑表单，新增为'add'，编辑为'edit'

      selectDateState: "1", // 日期统计方式（月，日，年）
      dateType: "daterange", // 日期类型（月，日，年）
      valueFormat: "yyyy-MM-dd", // 日期格式

      searchValidateForm: {
        id: "", //小组id
        groupName: "", //小组名
        departmentNames: "", //部门名
        groupLeader: "", //组长id
        groupLeaderName: "", //组长名
        memberName: "", //组员名
        memberNameId: "", //组员id
        position: "", //职位
        phone: "", //手机号
        departmentId: "", //部门id
      }, // 搜索表单验证对象
      searchVF: {
        robotCode: "", // 机器人编号
        spotId: "", // 景区id
        faultName: "", // 故障名称
        faultTimeType: "", // 故障时间类型
        startEndDate: "", // 起止日期
        warrantyState: "", // 质保状态
        faultState: "", // 故障状态
        signState: "", // 配件签收状态
      }, // 搜索需要提交的表单
      validateForm: {}, // 表单验证对象
      editValidateForm: {}, // 表单验证对象
      searchPartnerCompanyName: "", // 搜索的公司
      searchScenicName: "", // 搜索的景区
      searchSelectDateState: "1", // 搜索的日期统计方式
      searchStartEndDate: "", // 搜索的起止时间
      searchPayState: "", // 搜索的支付状态
      companys: [], // 公司列表
      companyState: "", // 当前请选择公司

      scenics: [], // 景区列表
      scenicState: "", // 当前请选择景区

      downloadUrl: "",

      total: 0,

      currentEditRowId: null, // 当前编辑的行id

      uploadDialogImageUrl: "",
      uploadDialogVisible: false,
      uploadDisabled: false, // 是否隐藏下载和删除按钮

      limitUpload: 1,
      file: null, // 上传的Excel文件
      fileTemp: null, // 上传的临时Excel文件
      fileListUpload: [], // 上传Excel文件列表
      modelScenicSpotName: [],

      // 搜索表单规则
      searchValidateFormRules: {},

      // 提交表单规则
      validateFormRules: {
        memberName: [
          {
            required: true,
            message: "请输入人员姓名",
            trigger: "blur",
          },
        ],
        departmentNames: [
          { required: true, message: "请输入所属部门", trigger: "blur" },
        ],
        position: [
          {
            required: true,
            message: "请输入职位",
            trigger: "blur",
          },
        ],
        phone: [
          {
            required: true,
            message: "请输入手机号",
            trigger: "blur",
          },
        ],
      },

      // 提交编辑表单规则
      editValidateFormRules: {},
      editDataRow: {},
      //优先级
      priorityList: [
        {
          value: "1",
          label: "A",
        },
        {
          value: "2",
          label: "B",
        },
        {
          value: "3",
          label: "C",
        },
      ],
      departmentNamelist: [],
      selectedDepartment: "", // 存储选中的部门名称
      correspondingID: null, // 存储对应的ID
      getday: false,
      days: "",
      startTime: "",
      endTime: "",
      timenode: [null, null],
      initTimeModle: new Date(),
      projectstattime: [],
      stattimes: [], //因为对话框界面的时间节点都是受timechange变化而变化的，所以如果想要编辑界面的时间显示之前的数据就必须要新建一个数组，这个数组重新把表单里的数据拿到
    };
  },
  created() {
    //获取表格数据
    this.initTableData();
  },
  beforeMount() {},
  mounted() {},
  methods: {
    // 登录(必须)
    // async login() {
    //   const { data: res } = await this.$http.post(
    //     "/loginSystem?username=houjingchen&password=houjingchen"
    //   );
    // },

    // async touristsGetToken() {
    //   const { data: res } = await this.$http.get(
    //     "https://load.topsroboteer.com/system/fourrobot/queryRobotDataList.do?scenicSpotId=72809461224343",
    //     {
    //       params: {},
    //     }
    //   );
    // },

    /**
     * 初始化表格数据(这个版本不会报错)
     */
    async initTableData() {
      const { data: res } = await this.$http.get(
        "/projectManagement/userManage/groupMember/page",
        {
          params: {
            memberName: this.searchValidateForm.memberName,
            pageNum: this.currentPage, // 页码
            currentPage: this.currentPage,
            pageSize: this.pageSize, // 每页条数
          },
        }
      );
      this.tableData = res.data;
      this.total = res.totals;
      this.departmentNamelistData();
    },

    //调用部门接口
    async departmentNamelistData() {
      const { data: res } = await this.$http.get(
        "/projectManagement/userManage/department/page",
        {
          params: {
            pageNum: this.currentPage, // 页码
            currentPage: this.currentPage,
            pageSize: this.pageSize, // 每页条数
          },
        }
      );
      this.departmentNamelist = res.data;
    },
    async handleDepartmentChange() {
      this.validateForm.departmentNames = this.selectedDepartment;
      this.correspondingID = await this.findDepartmentID(
        this.selectedDepartment
      );
      this.validateForm.departmentId = this.correspondingID;
    },
    async findDepartmentID(departmentName) {
      const index = this.departmentNamelist.findIndex(
        (item) => item.departmentName === departmentName
      );
      if (index !== -1) {
        return this.departmentNamelist[index].id;
      } else {
        return null;
      }
    },

    // 重置
    onReset() {
      // 清空所有输入框内容
      this.searchValidateForm.memberName = "";
      this.searchValidateForm.departmentNames = "";
      this.searchValidateForm.position = "";
      this.searchValidateForm.phone = "";
      // 重新获取搜索景区列表
      this.initTableData();
      // this.touristsGetToken();
    },

    // 点击查询数据按钮时触发
    onSearch(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.showTable = true;
          this.getTableData();
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    /**
     * 查询公司名称获取列表数据
     */
    getTableData() {
      // 否则调用接口，请求数据，为tableData重新赋值
      console.log("调用接口");
      this.initTableData();
    },

    /**
     * 新增
     */
    onAdd() {
      this.dialogVisible = true;
      this.dialogState = "add";
      this.dialogTitle = "新增人员信息";
      this.validateForm = {};
      this.selectedDepartment = "";
    },

    /**
     * 新增表格数据
     */
    async addTableData() {
      const { data: res } = await this.$http({
        method: "post",
        url: "/projectManagement/userManage/groupMember/save",
        data: this.validateForm,
        dataType: "json",
      });

      if (res.state == "200") {
        this.$message({
          message: res.msg,
          type: "success",
        });
        this.initTableData();
        // this.validateForm = {}; // 重置表单
      } else {
        this.$message({
          message: res.msg,
          type: "error",
        });
      }
    },

    // 点击编辑时触发

    editRow(index, row) {
      this.dialogVisible = true; // 是否显示编辑dialog对话框
      // 显示 dialog 弹窗
      this.dialogState = "edit";
      var rowObj = Object.assign({}, row); // 深拷贝row对象
      this.validateForm = rowObj;
      this.selectedDepartment = this.validateForm.departmentNames;
      this.dialogTitle = "编辑小组信息";
    },
    /**
     * 修改表格数据
     */
    async editTableData() {
      const { data: res } = await this.$http({
        method: "post",
        url: "/projectManagement/userManage/groupMember/edit",
        data: this.validateForm,
        dataType: "json",
      });
      console.log(res);
      if (res.state == "200") {
        this.$message({
          message: res.msg,
          type: "success",
        });
        this.initTableData();
      } else {
        this.$message({
          message: res.msg,
          type: "error",
        });
      }
    },

    //点击删除触发
    deleteRow(index, row) {
      this.$confirm("此操作将删除该行数据, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(async () => {
          this.deleteTableDate(row.id);
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
            offset: 100,
            center: true,
            duration: 1000,
          });
        });
    },
    async deleteTableDate(id) {
      const { data: res } = await this.$http({
        method: "post",
        url: "/projectManagement/userManage/groupMember/remove",
        data: { id: id },
      });

      // console.log(res);
      if (res.state == "200") {
        this.$message({
          type: "success",
          message: "删除成功",
          offset: 100,
          center: true,
          duration: 1000,
        });
        this.initTableData();
      } else {
        this.$message({
          type: "error",
          message: res.msg,
          offset: 100,
          center: true,
          duration: 3000,
        });
      }
    },

    // 新增 dialog 取消
    dialogClose() {
      this.dialogVisible = false;
      this.$refs.validateForm.resetFields(); // 重置表单（清空无效）
      // this.getValidateForm() // 将validateForm对象中各个属性清空
    },

    // 编辑 dialog 取消
    editDialogClose() {
      this.editDialogVisible = false;
      this.$refs.editValidateForm.resetFields(); // 重置表单（清空无效）
      this.editValidateForm = {};
      // this.getValidateForm() // 将validateForm对象中各个属性清空
    },

    // dialog 右上角关闭按钮
    handleClose(done) {
      done(); // 关闭dialog对话框
      this.$refs.validateForm.resetFields(); // 重置表单（清空无效）
      // this.getValidateForm() // 将validateForm对象中各个属性清空
    },

    // 编辑 dialog 右上角关闭按钮
    editHandleClose(done) {
      done(); // 关闭dialog对话框
      this.$refs.editValidateForm.resetFields(); // 重置表单（清空无效）
      this.editValidateForm = {};
    },

    validatePhone() {
      const phonePattern = /^\d{11}$/; // 正则表达式，匹配11位数字
      if (!phonePattern.test(this.validateForm.phone)) {
        // 如果不匹配，显示错误信息
        this.$message.error("手机号必须是11位数字");
        return false;
      }
      return true;
    },

    // dialog提交
    submitForm(formName) {
      // 先验证手机号
      if (!this.validatePhone()) {
        console.log("手机号验证失败");
        return;
      }
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (this.dialogState == "add") {
            // 新增任务信息
            console.log("新增小组信息");
            this.addTableData();
            this.dialogVisible = false;
          } else if (this.dialogState == "edit") {
            // 编辑任务信息
            console.log("编辑小组信息");
            this.editTableData();
            this.dialogVisible = false;
          } else if (this.dialogState == "detail") {
            // 任务详情信息
            console.log("任务详情信息");
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },

    onSizeChange(pageSize) {
      this.pageSize = pageSize;
      this.initTableData();
    },
    onCurrentChange(currentPage) {
      this.currentPage = currentPage;
      this.initTableData();
    },

    // 文件状态改变时的钩子，添加文件、上传成功和上传失败时都会被调用
    handleChange(file, fileList) {
      console.log(file);
      this.fileTemp = file.raw;

      // 导入判断
      if (this.fileTemp) {
        if (
          this.fileTemp.type ==
            "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet" ||
          this.fileTemp.type == "application/vnd.ms-excel"
        ) {
          this.importfxx(this.fileTemp);
        } else {
          this.$message({
            type: "warning",
            message: "附件格式错误，请删除后重新上传！",
          });
        }
      } else {
        this.$message({
          type: "warning",
          message: "请上传附件！",
        });
      }
    },

    // 文件超出个数限制时的钩子
    handleExceed(files, fileList) {
      this.$message.warning(
        `当前限制选择 1 个文件，本次选择了 ${files.length} 个文件，共选择了 ${
          files.length + fileList.length
        } 个文件`
      );
    },

    // 文件列表移除文件时的钩子
    handleRemove(file, fileList) {
      this.fileTemp = null;
    },

    // 导入格式
    importfxx(obj) {
      let _this = this;
      // 通过DOM取文件数据
      this.file = obj;
      var rABS = false; //是否将文件读取为二进制字符串
      var f = this.file;
      var reader = new FileReader();
      //if (!FileReader.prototype.readAsBinaryString) {
      FileReader.prototype.readAsBinaryString = function (f) {
        var binary = "";
        var rABS = false; //是否将文件读取为二进制字符串
        var pt = this;
        var wb; //读取完成的数据
        var outdata;
        var reader = new FileReader();
        reader.onload = function (e) {
          var bytes = new Uint8Array(reader.result);
          var length = bytes.byteLength;
          for (var i = 0; i < length; i++) {
            binary += String.fromCharCode(bytes[i]);
          }
          var XLSX = require("xlsx");
          if (rABS) {
            wb = XLSX.read(btoa(fixdata(binary)), {
              //手动转化
              type: "base64",
            });
          } else {
            wb = XLSX.read(binary, {
              type: "binary",
            });
          }
          outdata = XLSX.utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]]); //outdata就是你想要的东西
          console.log(outdata);
          this.da = [...outdata];
          let arr = [];
          this.da.map((v) => {
            let obj = {
              robotCount: v["机器人数量"],
              type: v["发货进度"],
              applicantName: v["申请人"],
              factoryName: v["发货工厂"],
              consignorName: v["发货人"],
              receivingName: v["收货景区"],
              consigneeName: v["收货人"],
              consigneePhone: v["收货人电话"],
              receiningAddress: v["收货地址"],
              consignmentDate: v["发货时间"],
            };
            arr.push(obj);
          });
          // 将 arr 赋值给 tableData，来更新表格数据
          this.tableData = arr;
          return arr;
        };
        reader.readAsArrayBuffer(f);
      };

      if (rABS) {
        reader.readAsArrayBuffer(f);
      } else {
        reader.readAsBinaryString(f);
      }
    },
  },

  components: {
    // DownloadExcel: JsonExcel,
  },
  computed: {
    GetPriority() {
      return (row) => {
        console.log(row);
        if (row == 1) {
          return "A";
        } else if (row == 2) {
          return "B";
        } else if (row == 3) {
          return "C";
        }
      };
    },
    SpotName() {
      return (row) => {
        var spotData = [];
        row.appFlowPathSpotList.forEach((item) => {
          spotData.push(item.scenicSpotName);
        });
        // console.log(spotData)
        return spotData + "";
        // console.log(row.appFlowPathSpotList)
      };
    },
  },
};
</script>

